import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import AdminServices from 'services/api/AdminServices';
import { toast } from 'react-toastify';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import { BsPlusCircle } from 'react-icons/bs';
import db from 'libs/db';
import { FiMinusCircle } from 'react-icons/fi';

export default function RebalanceScreen(props) {
    const [loading, setLoading] = React.useState(true);

    const [portfolioDecryptData, setPortfolioDecryptData] = React.useState([]);
    const [fundData, setfundData] = React.useState({});
    const [orderFinalData, setOrderFinalData] = React.useState({});
    const [constituents, setconstituents] = React.useState({});

    const [fundType, setFundType] = React.useState(3)
    const [rebalanceKey, setRebalanceKey] = React.useState("")

    const isSip = props.location.state.type === 'sip'
    const code = props.location.state.code;
    React.useEffect(async () => {
        if (isSip) {
            await loadDataSIP()
        } else {
            await loadDataRebalance()
        }
        setLoading(false)
        console.log(isSip, code, portfolioDecryptData, fundData, orderFinalData, constituents, fundType, rebalanceKey)
    }, []);

    const createRebalanceOrder = async (orderQuant, userid, mintboxid) => {
        var buycount = 0

        var securities = Object.entries(orderQuant).map((e) => {
            if (e[1].buySellFlag === 1) {
                buycount++
            }
            if (e[1].buySellFlag !== 0) {
                return ({ "ticker": e[1].FundCode, "quantity": Math.abs(e[1].TransactionUnits) + "", type: e[1].BuySellFlag === 1 ? "BUY" : "SELL" })
            }
        });
        securities = securities.filter(function (element) {
            return element !== undefined;
        });
        console.log(securities)
        if (buycount > 0) {
            var etfs = securities.map((i) => { return i.ticker })
            var etfsData = await AdminServices.getETFsById(etfs.toString()).then((res) => { return res })
            console.log(etfsData)
            etfsData = etfsData.data.map((etf) => {
                return {
                    ...etf,
                    ...securities.find((i) => i.ticker === etf.Symbol)
                }
            })
            setconstituents(etfsData)
            setOrderFinalData(securities)
        } else {
            setOrderFinalData([])
        }
    }

    const createSIPOrder = async (orderQuant, userid, mintboxid) => {
        var securities = Object.entries(orderQuant).map((e) => {
            return ({ "ticker": e[1].FundCode, "quantity": Math.abs(e[1].TransactionUnits) + "", type: e[1].BuySellFlag === 1 ? "BUY" : "SELL" })
        });
        securities = securities.filter(function (element) {
            return element !== undefined;
        });
        console.log(securities)
        var etfs = securities.map((i) => { return i.ticker })
        var etfsData = await AdminServices.getETFsById(etfs.toString()).then((res) => { return res })
        console.log(etfsData)
        etfsData = etfsData.data.map((etf) => {
            return {
                ...etf,
                ...securities.find((i) => i.ticker === etf.Symbol)
            }
        })
        setconstituents(etfsData)
        setOrderFinalData(securities)
    }

    const getRebalaceData = async (userId, mintboxId, goalID) => {
        var rebalanceMintbox = await AdminServices.rebalanceMintbox(userId, mintboxId, goalID).then((res) => { return res })
        console.log(rebalanceMintbox)
        setRebalanceKey(rebalanceMintbox.data[0].RebalanceKey)

        var res = await AdminServices.getMintboxById(mintboxId).then((res) => { return res })
        var d = await JSON.parse(res.data)
        setfundData(d[0].MintboxInfo[0])
        console.log(d[0].MintboxInfo[0])
        await createRebalanceOrder(rebalanceMintbox.data, userId, mintboxId)
    }

    const getSIPData = async (userId, mintboxId, goalID) => {
        var sipMintbox = await AdminServices.getScheduledSIPData(userId, mintboxId, goalID).then((res) => { return res })
        if(sipMintbox.type){
            console.log(sipMintbox)
            setRebalanceKey(sipMintbox.data[0].RebalanceKey)
        }

        var res = await AdminServices.getMintboxById(mintboxId).then((res) => { return res })
        var d = JSON.parse(res.data)
        setfundData(d[0].MintboxInfo[0])
        console.log(d[0].MintboxInfo[0])

        await createSIPOrder(sipMintbox.data, userId, mintboxId)
    }

    const getETFSIPData = async (userId, mintboxId, fundCode) => {
        var sipETF = await AdminServices.getScheduledETFSIPData(userId, mintboxId, fundCode).then((res) => { return res })
        console.log(sipETF)
        setRebalanceKey(sipETF.data[0].RebalanceKey)


        var res = await AdminServices.getETFInfoBySymbol(fundCode).then((res) => { return res })
        const aak = await JSON.parse(res.data).reduce((aa, f) => { return { ...aa, ...f } }, {})
        setfundData(aak)
        console.log(aak)

        await createSIPOrder(sipETF.data, userId, mintboxId)
    }


    const loadDataRebalance = async () => {
        setFundType(3)
        var res = await AdminServices.portfolioDecrypt(code).then((res) => { return res })
        console.log(res)
        var data = await res.data.split(":")
        console.log(data)
        setPortfolioDecryptData(data)
        try {
            db.get(db.model.user).then(async (p) => {
                if (Number(p.userData.id) === Number(data[0])) {
                    await getRebalaceData(Number(data[0]), Number(data[1]), Number(data[2]))
                } else {
                    toast.error('User mis-match!!')
                    props.history.replace("/Dashboard")
                }
            });
        } catch (e) {
            console.log(e)
            toast.error('Something went wrong! Please try again')
            props.history.replace("/menu/notifications")
        }
    }

    const loadDataSIP = async () => {
        var res = await AdminServices.portfolioDecrypt(code).then((res) => { return res })
        console.log(res)
        var data = await res.data.split(":")
        console.log(data)
        setPortfolioDecryptData(data)
        try {
            await db.get(db.model.user).then(async (p) => {
                if (Number(p.userData.id) === Number(data[0])) {
                    if (Number(data[1]) > 1) {
                        //mintbox SIP
                        setFundType(3)
                        await getSIPData(Number(data[0]), Number(data[1]), Number(data[2]))
                    } else {
                        //ETF sip
                        setFundType(1)
                        await getETFSIPData(Number(data[0]), Number(data[1]), data[2])
                    }
                } else {
                    toast.error('User mis-match!!')
                    props.history.replace("/Dashboard")
                }
            });
        } catch (e) {
            toast.error('Something went wrong! Please try again')
            props.history.replace("/menu/notifications")
        }
    }

    if (loading) {
        return (
            <div style={{ flex: 1 }}>
                <MainHeader
                    header={isSip ? 'SIP' : 'Rebalancing mintbox'}
                    navigation={props.history}
                />
                <ActivityLoaderMintbox />
            </div>
        );
    }
    else return (
        <div className='mainContainer'>
            <MainHeader
                navigation={props.history}
                header={isSip ? 'SIP' : 'Rebalancing mintbox'}
            />
            <div className="dataContainer px-0">
                <div style={{ display: 'flex', alignItems: "center", gap: '4%', padding: '4%' }}>
                    {
                        fundType === 1 ?
                            <AMCIcon amcName={fundData.ETFInfo.AMCName} height='40px' width='40px' />
                            :
                            <MintboxIcon id={fundData.MintboxId} height="40px" width="40px" />
                    }
                    <div style={{ flex: 1 }}>
                        <div className='header1dark'>
                            {fundType === 1 ? fundData.ETFInfo.ETFName : fundData.MintboxName}
                        </div>
                    </div>
                </div>
                <>
                    {/* <div className='bodyTextDark' style={{padding:'0 4%'}}>Rebalance update of 21st April, 2022</div> */}

                    {orderFinalData.length > 0 ?
                        <>
                            {orderFinalData.filter((item) => item.type === "BUY").length > 0 ?
                                <>
                                    <div style={{ display: 'flex', gap: '4%', alignItems: 'center', padding: '8% 4%' }}>
                                        {isSip ? <></> : <BsPlusCircle size={24} className='secondaryGreen' style={{ background: cssVar.getPropertyValue('--conservativeRisk'), borderRadius: "50%" }} />}
                                        <div className='header1dark'>{isSip ? 'SIP order' : 'Buy orders'}</div>
                                    </div>
                                    <div className='bodyTextLight' style={{ display: "flex", padding: "0 4% 1% 4%", borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
                                        <div style={{ flex: 1 }}>Constituent</div>
                                        <div>Quantity</div>
                                    </div>
                                    {constituents.filter((item) => item.type === "BUY").map((item) => {
                                        return <div key={item.ticker} style={{ display: "flex", gap: '4%', alignItems: 'center', padding: "3% 4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                                            <AMCIcon amcName={item.AMCName} height='24px' width='24px' />
                                            <div className='bodyTextDark' style={{ flex: 1 }}>
                                                <div>{item.AMCName}</div>
                                                <div>Ticker: {item.ticker}</div>
                                            </div>
                                            <div className='header1dark' style={{ paddingRight: "4%" }}>{item.quantity}</div>
                                        </div>
                                    })
                                    }
                                </>
                                :
                                <></>
                            }
                            {orderFinalData.filter((item) => item.type === "SELL").length > 0 ?
                                <>
                                    <div style={{ display: 'flex', gap: '4%', alignItems: 'center', padding: '8% 4%' }}>
                                        {isSip ? <></> : <FiMinusCircle size={24} style={{ color: cssVar.getPropertyValue('--peach'), background: cssVar.getPropertyValue('--highestRisk'), borderRadius: "50%" }} />}
                                        <div className='header1dark'>Sell orders</div>
                                    </div>
                                    <div className='bodyTextLight' style={{ display: "flex", padding: "0 4% 1% 4%", borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
                                        <div style={{ flex: 1 }}>Constituent</div>
                                        <div>Quantity</div>
                                    </div>
                                    {constituents.filter((item) => item.type === "SELL").map((item) => {
                                        return <div key={item.ticker} style={{ display: "flex", gap: '4%', alignItems: 'center', padding: "3% 4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                                            <AMCIcon amcName={item.AMCName} height='24px' width='24px' />
                                            <div className='bodyTextDark' style={{ flex: 1 }}>
                                                <div>{item.AMCName}</div>
                                                <div>Ticker: {item.ticker}</div>
                                            </div>
                                            <div className='header1dark' style={{ paddingRight: "4%" }}>{item.quantity}</div>
                                        </div>
                                    })
                                    }
                                </>
                                :
                                <></>
                            }
                        </>
                        :
                        <div className='header1light mt-5 text-center'>
                            No rebalance required
                        </div>
                    }
                    <div className='footer'>
                        <button
                            className='solidButton'
                            onClick={() => { }}
                        >
                            Rebalance my mintbox
                        </button>
                    </div>
                </>
            </div>

        </div >
    );
}

